






















































import { Component } from 'vue-property-decorator'
import NavIcon from '@/components/Base/NavIcon.vue'
import ThemeToggler from '@/components/Base/ThemeToggler.vue'
import Base from '@/components/Base'

import navigations from '@/utils/navigation'

@Component({
  name: 'LandingContent',
  components: {
    NavIcon,
    ThemeToggler,
  },
})
export default class LandingContent extends Base {
  feShow = true
  jsShow = false
  currentInterval: number | undefined
  show = false
  navs = navigations

  mounted(): void {
    this.currentInterval = setInterval(() => {
      this.toggle()
    }, 2500)
  }

  beforeDestroy(): void {
    clearInterval(this.currentInterval)
  }

  toggle(): void {
    this.feShow = !this.feShow
    this.jsShow = !this.feShow
  }
  toGithub(): void {
    this.url = 'github.com/vano20'
    this.openNewtab()
  }
  toLinkedin(): void {
    this.url = 'linkedin.com/in/savano-miatama/'
    this.openNewtab()
  }
  toTweet(): void {
    this.url = 'twitter.com/savano20'
    this.openNewtab()
  }
  toResume(): void {
    this.url =
      'docs.google.com/document/d/1lqqQAT6DRopJiVrfpjHKWnfEFP7McjQWavUvXbjHYIw/edit'
    this.openNewtab()
  }
}
