













import { Component, Vue } from 'vue-property-decorator'
import NavBar from '@/components/NavBar.vue'
import MainFooter from '@/components/Footer.vue'

@Component({
  components: {
    NavBar,
    MainFooter,
  },
})
export default class App extends Vue {
  get hideNav(): boolean {
    return !(this.$route.path === '/')
  }
}
